import http from "../index"
import { ReqPage, ResPage } from "../interface";
import { App } from "../interface/app";

// 新增应用
export const addApp = (params: App.CreateAppForm) => {
    return http.post<App.ResApp>('app', params);
};

// 获取应用列表
export const getAppList = (params: ReqPage) => {
    return http.get<ResPage<App.ResApp>>('app', params);
};

// 删除单个应用
export const deleteApp = (id: string) => {
    return http.delete<App.ResApp>(`app/${id}`);
};

// 获取应用信息
export const getAppInfo = (appId: string) => {
    return http.get<App.ResApp>(`app/${appId}`);
};