import { getAppInfo } from "@/api/modules/app";
import { CopyOutlined, UploadOutlined } from "@ant-design/icons";
import { Button, Card, Space, Table, message } from "antd";
import { ColumnsType } from "antd/es/table";
import Title from "antd/es/typography/Title";
import copy from 'copy-to-clipboard';
import { useEffect, useState } from "react";
import { useSearchParams } from "react-router-dom";
import AddSourcemapPage from "./addSourcemap";
import { getSourcemap, getSourcemapList } from "@/api/modules/web/sourcemap";
import { download } from "@/utils/util";

interface DataType {
    key: React.Key;
    filename: string;
    release: string;
    filesize: number;
    createdAt: string;
}

const SourceMapManage = () => {
    const [key, setKey] = useState<string>('');
    const [token, setToken] = useState<string>('');
    const [pageNum, setPageNum] = useState(1);
    const [pageSize, setPageSize] = useState(10);
    const [sourcemapTotal, setSourcemapTotal] = useState(0);
    const [sourcemapList, setSourcemapList] = useState<DataType[]>([]);
    const [searchParams] = useSearchParams();
    const [showCreatePage, setShowCreatePage] = useState(false);

    const initAppInfo = async () => {
        const appId = searchParams.get('appId');
        if (appId) {
            const appInfo = await getAppInfo(appId);
            if (appInfo && appInfo.code === 200) {
                setKey(appInfo.data!.apiKey);
                setToken(appInfo.data!.apiSecret);
            }
        }
    }

    useEffect(() => {
        initAppInfo();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const copyText = (e: React.MouseEvent, text: string) => {
        e.stopPropagation();
        copy(text);
        message.success('已复制')
    }

    const columns: ColumnsType<DataType> = [
        {
            title: '文件名',
            dataIndex: 'filename',
            width: 50,
        },
        {
            title: 'release',
            dataIndex: 'release',
            width: 100,
        },
        {
            title: '文件大小',
            dataIndex: 'filesize',
            width: 50,
        },
        {
            title: '上传时间',
            dataIndex: 'createdAt',
            width: 100,
        },
        {
            title: '操作',
            width: 100,
            render: (_, record) => (
                <Space size="middle">
                    <Button type="link" onClick={(e) => downloadSourceMap(e, record.key.toString())}>下载 Sourcemap</Button>
                </Space>
            ),
        },
    ];

    const downloadSourceMap = async (e: React.MouseEvent, smId: string) => {
        const result = await getSourcemap(smId);
        if (result.data) {
            download(`${result.data.filename}.js.map`, result.data.sourcemap);
        }
    }

    const initSourcemapList = async () => {
        const result = await getSourcemapList({ pageNum, pageSize });
        if (result.data) {
            setSourcemapTotal(result.data.total);
            const data: DataType[] = [];
            result.data.data.forEach(item => {
                data.push({
                    key: item.smId,
                    release: item.release,
                    createdAt: item.createdAt,
                    filename: item.filename,
                    filesize: item.filesize,
                })
            });
            setSourcemapList(data);
        }
    };

    useEffect(() => {
        initSourcemapList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    useEffect(() => {
        initSourcemapList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [pageNum, pageSize]);

    const pagination = {
        showSizeChanger: true,
        onChange: (page: number, pageSize: number) => {
            setPageNum(page);
            setPageSize(pageSize);
        },
        defaultCurrent: pageNum,
        total: sourcemapTotal
    }

    return (<div>
        <div style={{ background: '#ffffff', padding: '12px 16px' }}>
            <Title level={5}>自动上传密钥</Title>
            <div>
                <span><span style={{ color: '#86909c' }}>API Key:</span>&nbsp;&nbsp;{key} <CopyOutlined style={{ marginLeft: '8px' }} onClick={(e) => copyText(e, key)} /></span> <span style={{ marginLeft: '16px' }}> <span style={{ color: '#86909c' }}>API Token:</span>&nbsp;&nbsp;{token} <CopyOutlined style={{ marginLeft: '8px' }} onClick={(e) => copyText(e, token)} /></span>
            </div>
        </div>
        <Card title="sourcemap列表" style={{ marginTop: '20px' }}>
            <Button type="primary" icon={<UploadOutlined />} onClick={e => setShowCreatePage(true)}>
                上传Sourcemap
            </Button>
            <Table style={{ marginTop: '12px' }} pagination={pagination} columns={columns} dataSource={sourcemapList} />
        </Card>
        <AddSourcemapPage onSuccess={() => { setShowCreatePage(false); initSourcemapList(); }} open={showCreatePage} onClose={() => setShowCreatePage(false)}></AddSourcemapPage>
    </div>);
}

export default SourceMapManage;