import { Sourcemap } from "@/api/interface/web/sourcemap";
import http from "../../index"
import { ReqPage, ResPage } from "../../interface";

// 获取sourcemap列表
export const getSourcemapList = (params: ReqPage) => {
    return http.get<ResPage<Sourcemap.ResSourcemap>>('web/sourcemap', params);
};

// 新增sourcemap信息
export const addSourcemap = (params: Sourcemap.CreateSourcemap) => {
    return http.post<ResPage<Sourcemap.ResSourcemap>>('web/sourcemap', params);
};

// 获取sourcemap
export const getSourcemap = (id: string) => {
    return http.get<Sourcemap.ResSourcemap>(`web/sourcemap/${id}`);
};