import { addSourcemap } from "@/api/modules/web/sourcemap";
import { CloseOutlined, InboxOutlined } from "@ant-design/icons";
import { Button, Drawer, Form, Input, Select, Space, UploadFile, UploadProps, message } from "antd";
import Dragger from "antd/es/upload/Dragger";
import { useState } from "react";
import { useSearchParams } from "react-router-dom";

interface Props {
    open: boolean;
    onSuccess: () => void;
    onClose: () => void
}

const AddSourcemapPage: React.FC<Props> = (props: Props) => {
    const [form] = Form.useForm();
    const [fileList, setFileList] = useState<UploadFile[]>([]);
    const [sourcemap, setSourcemap] = useState<string>('');
    const [searchParams] = useSearchParams();
    const [messageApi, contextHolder] = message.useMessage();

    const onFinish = async (values: any) => {
        const appId = searchParams.get('appId');
        if (!appId) {
            messageApi.open({
                type: 'error',
                content: 'appId不能为空!',
            });
            return;
        }
        try {
            await addSourcemap({
                filename: values.name,
                sourcemap: sourcemap,
                site_type: 'web',
                appId: appId,
                release: values.release
            });
            //清空form
            form.resetFields();
            message.success("创建成功");
            props.onSuccess();
        } catch (error) {
            console.log(error);
        }
    };

    const uploadProps: UploadProps = {
        accept: '.js.map',
        multiple: false,
        maxCount: 1,
        onRemove: (file) => {
            const index = fileList.indexOf(file);
            const newFileList = fileList.slice();
            newFileList.splice(index, 1);
            setFileList(newFileList);
        },
        beforeUpload: (file) => {
            const reader = new FileReader();
            // result 属性中将包含一个字符串以表示所读取的文件内容。
            reader.readAsBinaryString(file);
            reader.onload = function () {
                setSourcemap(this.result as string);
            }
            setFileList([...fileList, file]);
            return false;
        },
        fileList,
    };

    const onChange = (value: string) => {
        console.log(`selected ${value}`);
    };

    const onSearch = (value: string) => {
        console.log('search:', value);
    };

    const normFile = (e: any) => {
        if (Array.isArray(e)) {
            return e;
        }
        return e?.fileList;
    };

    return (
        <Drawer
            title="上传SourceMap"
            placement='right'
            width={500}
            closable={false}
            open={props.open}
            extra={
                <Space>
                    <CloseOutlined onClick={props.onClose} />
                </Space>
            }
        >
            {contextHolder}
            <Form
                layout="vertical"
                form={form}
                onFinish={onFinish}
                initialValues={{ type: 1 }}
            >
                <Form.Item label="js sourcemap文件名" name="name" rules={[{ required: true }]}>
                    <Input />
                </Form.Item>
                <Form.Item label="上传 Sourcemap 文件" name="dragger" rules={[{ required: true }]}>
                    <Form.Item name="dragger" valuePropName="fileList" getValueFromEvent={normFile} noStyle>
                        <Dragger {...uploadProps}>
                            <p className="ant-upload-drag-icon">
                                <InboxOutlined />
                            </p>
                            <p className="ant-upload-text">点击或拖拽文件到此处上传</p>
                            <p className="ant-upload-hint">
                                只支持.js.map文件
                            </p>
                        </Dragger>
                    </Form.Item>
                </Form.Item>
                <Form.Item label="Release" name="release" rules={[{ required: true }]}>
                    <Select
                        showSearch
                        allowClear
                        placeholder="选择或输入对应版本"
                        optionFilterProp="children"
                        onChange={onChange}
                        onSearch={onSearch}
                        filterOption={(input, option) =>
                            (option?.label ?? '').toLowerCase().includes(input.toLowerCase())
                        }
                        options={[
                            {
                                value: 'jack',
                                label: 'Jack',
                            },
                            {
                                value: 'lucy',
                                label: 'Lucy',
                            },
                            {
                                value: 'tom',
                                label: 'Tom',
                            },
                        ]}
                    />
                </Form.Item>
                <Form.Item>
                    <Button type="primary" htmlType="submit">
                        确定
                    </Button>
                </Form.Item>
            </Form>
        </Drawer>
    );
}

export default AddSourcemapPage;