import { MenuItem } from "@/layout/components/SiderMenu";

/**
* @description 递归当前路由的 所有 关联的路由，生成面包屑导航栏
* @param {String} path 当前访问地址
* @param {Array} menuList 菜单列表
* @returns array
*/
export const getBreadcrumbList = (path: string, menuList: MenuItem[]) => {
	let tempPath: any[] = [];
	try {
		const getNodePath = (node: MenuItem) => {
			tempPath.push(node);
			// 找到符合条件的节点，通过throw终止掉递归
			if (node?.key === path) {
				throw new Error("GOT IT!");
			}
			if (node?.['children'] && node?.['children'].length > 0) {
				for (let i = 0; i < node?.['children'].length; i++) {
					getNodePath(node['children'][i]);
				}
				// 当前节点的子节点遍历完依旧没找到，则删除路径中的该节点
				tempPath.pop();
			} else {
				// 找到叶子节点时，删除路径当中的该叶子节点
				tempPath.pop();
			}
		};
		for (let i = 0; i < menuList.length; i++) {
			getNodePath(menuList[i]);
		}
		return [];
	} catch (e) {
		return tempPath.map(item => item.label) as string[];
	}
};

export function download(filename: string, content: string) {
	const arraybuffer = new TextEncoder().encode(content).buffer;
	const blob = new Blob([arraybuffer], { type: 'text/plain;charset=utf-8' });
	const reader = new FileReader();
	reader.readAsDataURL(blob);
	reader.onload = (event: any) => {
		const element: any = document.createElement('a');
		element.href = event.target.result;
		element.download = filename;
		document.body.appendChild(element);
		element.click();
		document.body.removeChild(element);
	};
}